    <bee-button @click="addLoading1(0)">第一种</bee-button>
    <bee-button theme="success" @click="addLoading1(1)">第二种</bee-button>
    <bee-button theme="error" @click="addLoading1(2)">第三种</bee-button>
  </div>
</template>

</div>
                
