<h3>options</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>parent</td>
<td>要添加的节点</td>
<td>-</td>
<td>—</td>
<td>body</td>
</tr>
<tr>
<td>type</td>
<td>类型</td>
<td>number</td>
<td>0,1,2</td>
<td>0</td>
</tr>
<tr>
<td>display</td>
<td>文字和loading之间的排列方式</td>
<td>string</td>
<td>block,inline</td>
<td>block</td>
</tr>
<tr>
<td>text</td>
<td>加载提示的文字</td>
<td>string</td>
<td>—</td>
<td>加载中</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.inline .btn--wp {
  margin: 0 10px 10px 0;
}
.loading-wrapper {
  padding: 20px 0;
}
</style>
<script>
export default {
  data () {
    return {
      laoding: [],
      loading1: false
    }
  },
  methods: {
    addLoading1 (type) {
      const item = this.$_createLoading({
        type: type
      })

      item.show()

      window.setTimeout(() => {
        item.hide()
      }, 3000)
    },
    toggleLoading () {
      this.loading1 = true

      setTimeout(() => {
        this.loading1 = false
      }, 2000)
    }
  }
}
</script>